<!-- 新闻主内容 -->
<template>
	<div id="NewsList">
    	<div class="bod-container">
	        <ul class="bod-warp">

	            <li class="bod-list clearfix" v-for="item in dataArr" >
	                <!-- <div class="bod-list-img float-lft" @click="goDetail()">
	                    <img class="list-img width100 " src="../../../assets/img/list-img.png"/>
	                </div>    -->             
	                <div class="bod-list-info float-rigt">
	                    <p class="bod-list-title" @click="goDetail()">{{item.title}}</p>
	                    <ul class="source-wrap clearfix">
	                        <li class="float-lft source-text">来源：<span>{{item.src}}</span></li>
	                        <li class="float-rigt concern text-cent"  
	                        :class="{'guanzhu':item.isfollow}"
	                        @click="goFollow()">{{ item.isfollow?'已关注':'关注' }}</li>
	                    </ul>
	                    <ul class="time-wrap clearfix">
	                        <li class="float-lft">{{item.pdate_src}}</li>
	                        <!-- <li class="float-rigt text-rigt message"><img class="message-ico" src="../assets/img/message.png" />412</li> -->
	                        <li class="float-rigt text-rigt check"><img class="check-ico" src="../../../assets/img/check.png" />1108</li>
	                    </ul>
	                </div>
	            </li>
 
	        </ul>
    	</div>
	</div>

</template>
<style type="text/css" scoped>
	.bod-list .bod-list-info { width: 100%; }
	.concern{ width: 38px; }
	.guanzhu {     border: 1px solid #7b7d7f;color: #6a6a6a; }
</style>
<script>
	const dataSrc = JSON.parse(sessionStorage.datas) //数据源
	console.log(dataSrc);
	export default {
		name:'newsList',
		data(){
			return {
				dataArr:null
			}
		},
		methods:{
			goDetail:function(){
				console.log("去详细页");
			},
			goFollow:function(){
				console.log("点击关注");	
				// this.Mimt
			}
		},
		components:{
			
			

		},
		mounted(){
			 this.dataArr = dataSrc.result;
			

		}




	}
</script>


