<!-- 资讯页内的关注项组件 -->
<template>
	<div id="Concern">
		<div class="bod-container">
	        <ul class="bod-warp">
	            <li class="bod-list clearfix" 
	            	v-for="item in datas">
	                <!-- <div class="bod-list-img float-lft">
	                    <img class="list-img width100 " src="../../assets/img/list-img.png"/>
	                </div>  -->               
	                <div class="bod-list-info float-rigt">
	                    <div class="concern-wrap">
	                        <!-- <span class="concern-type displ-inl-blk"></span> -->
	                        <p class="bod-list-title-concern displ-inl-blk">{{item.title}}</p>
	                    </div>
	                    <ul class="source-wrap clearfix">
	                        <li class="float-lft source-text">来源：<span>{{item.src}}</span></li>
	                        <li class="float-rigt concerned text-cent">✓&nbsp;已关注</li>
	                    </ul>
	                    <ul class="time-wrap clearfix">
	                        <li class="float-lft">{{item.date}}</li>
	                        <!-- <li class="float-rigt text-rigt message"><img class="message-ico" src="img/message.png" />412</li> -->
	                        <!-- <li class="float-rigt text-rigt check"><img class="check-ico" src="../../assets/img/check.png" />1108</li> -->
	                    </ul>
	                </div>
	            </li>
	            
	        </ul>
        
    	</div>
		 
	</div>
</template>
<style type="text/css" scoped>
	.bod-list .bod-list-info { width: 100%; }
	.concern{ width: 38px; }
	.guanzhu {     border: 1px solid #7b7d7f;color: #6a6a6a; }
	.nav-wrap { border-bottom: 1px solid #ddd; }
	.source-wrap { margin:0; }
</style>
<script type="text/javascript">
const dataSrc = JSON.parse(sessionStorage.datas) //数据源
	console.log(dataSrc);
	export default {
		name:'Concern',
		data(){
			return {
				datas:null
			}
		},
		methods:{
			goDetail:function(){
				console.log("去详细页");
			},
			goFollow:function(){
				console.log("点击关注");	
				// this.Mimt
			}
		},
		components:{
			
			

		},
		mounted(){
			 this.datas = dataSrc.result;
			

		}

	}
</script>


