<!-- 集市下面的营养饲料组件 -->
<template>
	<div id="YingYangSiLiao">
         <v-headr :myname="myname"></v-headr>
		<section class="aui-scrollView YingYangSiLiao">  
                <div class="aui-card-list"> 
                    <div class="area-title"><h4><span class="more">更多</span>热门品种</h4></div>
                    <ul class="yy-mod">
                        <li class="main_cat active">生猪</li> 
                        <li class="main_cat">活鸡</li>
                        <li class="main_cat">花生粕</li>
                        <li class="main_cat">豆粕</li>
                        <li class="main_cat">DDGS</li>
                        <li class="main_cat">菜粕</li>
                        <li class="main_cat">玉米胚芽粕</li>
                        <li class="main_cat">玉米纤维</li>
                    </ul>

                    <div class="area-title"><h4><span class="more">更多</span>最新价格</h4></div>
                   <a href="javascript:void(0);" class="aui-panel-cell">
                        <div class="aui-panel-cell-bd">
                            <h4>辽宁生猪场价格</h4>
                            <span>2018/0/21</span>
                        </div> 
                        <div class="float-rigt">
                            <em class="jiage">154元<span>↑</span></em>
                        </div>
                    </a>
                </div>
                

            </section>
	</div>
</template>

<script type="text/javascript">
    import Headr from '../../components/header.vue'
	export default {
		name:'YingYangSiLiao',
		data(){
			return {
				msg:'集市下面的优质饲料组件',
                myname:{
                    name:'营养饲料',
                    IsSubPage:true
                },
			}
		},
        components:{
            'v-headr':Headr,
        },
		methods:{
			chilClick(){
				this.$emit('childByValue',this.IsSubPage)
			}
		},
		mounted(){
			this.chilClick()
		}
	}
</script>


